// Footer
.footer {
    padding: 80px;
    background: linear-gradient(90deg, $mainColor, $brandColor);
    color: #fff;
    h4 {
        font: {
            size: 20px;
            weight: 600
        }
        text-transform: uppercase;
        margin-bottom: 20px;
        margin-top: 40px;
        text-shadow: 0px 2px 0px #000;
    }
    .text-footer {
        p {
            color: #fff;
            font-size: 13px
        }
    }
    .contact-us {
        ul {
            list-style: none;
            margin: 0;
            padding: 0;
            li {
                margin-bottom: 10px;
                font-size: 14px;
                i {
                    font-size: 20px;
                    width: 35px;
                    height: 35px;
                    line-height: 35px;
                    text-align: center;
                    margin-right: 10px;
                    background: linear-gradient(90deg, $mainColor, $brandColor);
                }
            }
        }
    }
    .get-in-touch {
        input {
            display: block;
            margin-bottom: 20px;
            &:nth-child(1) {
                width: 90%;
                border: none;
                border-radius: 5px;
                padding: 5px 8px;
            }
            &:nth-child(2) {
                border: none;
                border-radius: 5px;
                background: linear-gradient(90deg, $mainColor, $brandColor);
                padding: 5px 10px;
                width: auto;
                color: #fff;
                text-transform: uppercase;
                letter-spacing: 1px;
                font-size: 14px;
                font-weight: 600;
                transition: .3s;
                &:hover {
                    filter: blur(1px)
                }
            }
            &:focus {
                outline: 0;
                box-shadow: none;
            }
        }
    }
}