// Navbar
nav {
    .navbar {
        width: 250px;
        height: 100%;
        position: fixed;
        top: 0;
        left: 0;
        border-radius: 0 10% 40% 0;
        transform: translateX(-180px);
        transition: .3s linear;
        background-color: #f1f1f1;
        z-index: 999;
        border-right: 1px solid $brandColor;
        @media #{$mediaExSmall} {
            width: 100%;
            transform: translateX(-85%);
        }
        @media #{$mediaSmall} {
            width: 100%;
            transform: translateX(-90%);
        }
        @media #{$mediaMedium} {
            width: 350px;
            transform: translateX(-82%);
        }
        @media #{$mediaLarge} {
            width: 300px;
            transform: translateX(-230px);
        }
        .navbar-icon {
            width: 30px;
            height: 30px;
            background-color: transparent;
            position: absolute;
            right: 20px;
            top: 30px;
            border-radius: 10px;
            cursor: pointer;
            span {
                display: block;
                width: 100%;
                height: 2px;
                border-radius: 40px;
                background-color: $mainColor;
                position: absolute;
                transition: .3s linear;
                &:nth-child(1) {
                    top: 0;
                }
                &:nth-child(2) {
                    top: 8px;
                }
                &:nth-child(3) {
                    top: 16px;
                }
            }
        }
        .navbar-nav {
            padding: 15px 80px;
            @media #{$mediaExSmall} {
                padding: 5px 40px;
            }
            @media #{$mediaSmall} {
                padding: 10px 60px;
            }
            @media #{$mediaMedium} {
                padding: 15px 60px;
            }
            .nav-item {
                list-style: none;
                padding: 10px;
                font-size: 18px;
                text-transform: uppercase;
                @media #{$mediaExSmall} {
                    font-size: 15px;
                }
                @media #{$mediaSmall} {
                    font-size: 16px;
                }
                @media #{$mediaMedium} {
                    font-size: 17px;
                }
                .nav-link {
                    color: #000;
                    text-decoration: none;
                    transition: .2s;
                    &:hover {
                        color: $mainColor
                    }
                }
            }
        }
    }
    .navbar-brand {
        position: absolute;
        top: 15px;
        right: 10px;
        font-size: 25px;
        letter-spacing: 1px;
        color: #fff;
        text-transform: uppercase;
        z-index: 99;
        transition: .3s linear;
        text-shadow: 3px 1px 7px #000;
        @media #{$mediaExSmall} {
            font-size: 20px;
        }
        @media #{$mediaSmall} {
            font-size: 22px;
        }
        @media #{$mediaMedium} {
            font-size: 23px;
        }
        &:hover {
            color: $mainColor
        }
    }
}

.open {
    transform: translateX(0) !important;
    border-radius: 0 5% 5% 0 !important;
    @media #{$mediaExSmall} {
        border-radius: 0 !important;
    }
    @media #{$mediaSmall} {
        border-radius: 0 !important;
    }
    @media #{$mediaMedium} {
        border-radius: 0 !important;
    }
}

.navbar-icon.move span:nth-child(1) {
    top: 15px !important;
    -webkit-transform: rotate(135deg);
    -moz-transform: rotate(135deg);
    -o-transform: rotate(135deg);
    transform: rotate(135deg);
}

.navbar-icon.move span:nth-child(2) {
    width: 0;
    opacity: 0;
}

.navbar-icon.move span:nth-child(3) {
    top: 15px !important;
    -webkit-transform: rotate(-135deg);
    -moz-transform: rotate(-135deg);
    -o-transform: rotate(-135deg);
    transform: rotate(-135deg);
}