// What We Do
.w2-d {
    padding: 80px;
    margin: 80px 0;
    .head {
        text-align: center;
        margin-bottom: 60px;
        position: relative;
        h2 {
            text-transform: uppercase;
            font-weight: 600;
            letter-spacing: 1px;
            word-spacing: 1px;
            text-shadow: 1px 1px 1px #000;
        }
        &::after {
            content: '';
            position: absolute;
            border-bottom: 3px solid $mainColor;
            width: 15%;
            left: 50%;
            transform: translate(-50%, -50%);
            border-radius: 20px
        }
        &::before {
            content: '';
            position: absolute;
            border-bottom: 6px solid $mainColor;
            width: 5%;
            left: 50%;
            bottom: -37%;
            transform: translate(-50%, -50%);
            border-radius: 20px
        }
    }
    .w2-d-text {
        text-align: center;
        margin-bottom: 90px;
        h4 {
            font-size: 21px;
            font-weight: 600;
            text-transform: uppercase;
            letter-spacing: 1px;
            margin-bottom: 20px;
            text-shadow: 0px 0px .5px  rgba(#000, .5);
        }
        p {
            font-size: 13px;
        }
    }
    .w2-d-btn {
        text-align: center;
        .btn {
            font-size: 14px;
            border-radius: 20px;
            background: linear-gradient(90deg, $mainColor, $brandColor);
            color: #fff;
            width: 120px;
            transition: .3s linear;
            &:hover {
                filter: blur(.8px);
            }
            &:focus {
                outline: none;
                box-shadow: none;
            }
        }
    }
}


