// Socail Media
.social-media {
    padding: 80px;
    background: #f5f2ff;
    .head {
        &::after {
            border-bottom: none
        }
        &::before {
            border-bottom: none
        }
    }
    .icon-social {
        list-style: none;
        text-align: center;
        padding: 0;
        li {
            display: inline-block;
            font-size: 30px;
            line-height: 60px;
            padding: 0 15px;
            margin: 0 10px;
            width: 60px;
            height: 60px;
            color: #fff;
            border-radius: 50%;
            transition: .3s;
            cursor: pointer;
            margin-bottom: 5px;
            &:nth-child(1) {
                background-color: #3b5999
            }
            &:nth-child(2) {
                background-color: #55acee
            }
            &:nth-child(3) {
                background-color: #0077B5
            }
            &:nth-child(4) {
                background-color: #25D366
            }
            &:nth-child(5) {
                background-color: #e4405f
            }
            &:nth-child(6) {
                background-color: #131418
            }
            &:hover {
                filter: blur(1px);
                transform: scale(1.1)
            }
        }
    }
}