.loading-screen {
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    width: 100%;
    height: 100%;
    background-color: $brandColor;
    z-index: 9999;
    .loading {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
        height: 40px;
        display: flex;
        align-items: center;
        .obj {
            width: 6px;
            height: 40px;
            background-color: white;
            margin: 0 3px;
            border-radius: 10px;
            animation: loading .8s infinite;
            &:nth-child(2) {
                animation-delay: .1s;
            }
            &:nth-child(3) {
                animation-delay: .2s;
            }
            &:nth-child(4) {
                animation-delay: .3s;
            }
            &:nth-child(5) {
                animation-delay: .4s;
            }
            &:nth-child(6) {
                animation-delay: .5s;
            }
            &:nth-child(7) {
                animation-delay: .6s;
            }
            &:nth-child(8) {
                animation-delay: .7s;
            }
        }
    }
}


@include keyFrame(loading) {
    0% {
        height: 0px
    }
    50% {
        height: 40px
    }
    100% {
        height: 0px
    }
}