body {
    font-family: 'Open Sans', sans-serif;
    overflow: hidden;
}

// H2
.head {
    text-align: center;
    margin-bottom: 60px;
    position: relative;
    h2 {
        text-transform: uppercase;
        font-weight: 600;
        letter-spacing: 1px;
        word-spacing: 1px;
        text-shadow: 1px 1px 1px #000;
    }
    &::after {
        content: '';
        position: absolute;
        border-bottom: 3px solid $mainColor;
        width: 15%;
        left: 50%;
        transform: translate(-50%, -50%);
        border-radius: 20px
    }
    &::before {
        content: '';
        position: absolute;
        border-bottom: 6px solid $mainColor;
        width: 5%;
        left: 50%;
        bottom: -37%;
        transform: translate(-50%, -50%);
        border-radius: 20px
    }
}

// Pargraph
p {
    font-size: 14px;
    line-height: 25px;
    text-transform: capitalize;
    color: #555;
    text-shadow: 0px 0px 0.5px rgba(85, 85, 85, 0.5);
}