// Header
.header {
    background: linear-gradient(90deg, $mainColor, $brandColor);
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    @media #{$mediaExSmall} {
        height: 100%
    }
    @media #{$mediaSmall} {
        height: 100%
    }
    @media #{$mediaMedium} {
        height: 100%
    }
    .header-text {
        margin: 0px 30px;
        color: #fff;
        display: flex;
        justify-content: center;
        align-items: center;
        height: 100%;
        @media #{$mediaExSmall} {
            margin: 50px 80px;
            width: auto;
        }
        @media #{$mediaSmall} {
            margin: 50px 80px;
            width: auto;
        }
        @media #{$mediaMedium} {
            margin: 100px 50px;
            width: auto;
        }
        @media #{$mediaLarge} {
            margin: 0px 50px;
        }
        h1 {
            font-size: 35px;
            font-weight: 600;
            text-transform: uppercase;
            letter-spacing: 1px;
            margin-bottom: 20px;
            text-shadow: 0px 2px 0px #000;
            @media #{$mediaExSmall} {
                font-size: 20px;
            }
            @media #{$mediaSmall} {
                font-size: 25px;
            }
            @media #{$mediaMedium} {
                font-size: 28px;
            }
            @media #{$mediaLarge} {
                font-size: 30px;
            }
        }
        p {
            font-size: 14px;
            color: #fff;
            line-height: 1.8;
            word-spacing: 1px;
            text-shadow: 0px 2px 0px #000;
            @media #{$mediaExSmall} {
                font-size: 12px;
            }
            @media #{$mediaSmall} {
                font-size: 13px;
            }
            @media #{$mediaMedium} {
                font-size: 13px;
            }
            @media #{$mediaLarge} {
                font-size: 13px;
            }
        }
    }
}